import { OverlayTrigger, Tooltip, Badge } from 'react-bootstrap';
import './LaborContractStage.css';


function PersonalInfoAgreement() {

  const 근태정보Tooltip = (props) => (
    <Tooltip id="근태정보-tooltip" {...props}>
      출퇴근 기록관리 장비에 따라<br/>바이오(지문), 모바일 위치정보 등으로 변경하세요.
    </Tooltip>
  );

  return (
    <>
      <div style={{
        position: "relative",
        width: "100%",
        margin: "0 auto",
        borderWidth: "1px",
        textAlign: "center"
      }}>
        <div style={{
          padding: "5px",
          border: "2px solid",
          display: "inline-block"
        }}>
          <div>
            <table cellSpacing="0" cellPadding="0" style={{ borderCollapse: 'collapse', border: 'none', tableLayout: 'fixed' }}>
              <tbody>
              <tr>
                <td colSpan="2" valign="middle" style={{
                  overflow: 'hidden',
                  width: '1000px',
                  height: '17px',
                  borderLeft: '#000000 0.425250pt solid',
                  borderRight: '#000000 0.425250pt solid',
                  borderTop: '#000000 0.425250pt solid',
                  borderBottom: '#000000 0.425250pt none',
                  padding: '1.4pt 5.1pt 1.4pt 5.1pt'
                }}>

                </td>
              </tr>
              <tr>
                <td colSpan="2" valign="middle" style={{
                  overflow: 'hidden',
                  width: '700px',
                  height: '25px',
                  borderLeft: '#000000 0.425250pt solid',
                  borderRight: '#000000 0.425250pt solid',
                  borderTop: '#000000 0.425250pt none',
                  borderBottom: '#000000 0.425250pt none',
                  padding: '1.4pt 5.1pt 1.4pt 5.1pt'
                }}>
                  <div className='mb-3'><span style={{ position: 'relative', fontSize: '16.0pt', fontWeight: 'bold' }}>출퇴근 기록 등 근태관리를 위한 개인정보 수집ㆍ이용 동의서</span></div>
                </td>
              </tr>
              <tr>
                <td colSpan="2" valign="middle" style={{
                  overflow: 'hidden',
                  width: '700px',
                  height: '25px',
                  borderLeft: '#000000 0.425250pt solid',
                  borderRight: '#000000 0.425250pt solid',
                  borderTop: '#000000 0.425250pt none',
                  borderBottom: '#000000 0.425250pt none',
                  padding: '1.4pt 5.1pt 1.4pt 5.1pt'
                }}>

                </td>
              </tr>
              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ marginLeft: '5.0pt', textAlign: 'left' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      1. 개인정보의 수집 이용 목적
                    </span>
                  </div>

                  <div className='mb-3' style={{ position: 'relative', marginLeft: '10.0pt', textAlign: 'left', fontSize: '13.0pt' }}>
                     근로기준법 제39조(사용증명서), 제42조(계약 서류의 보존) 및 제48조(임금대장 및 임금명세서) 준수를 위해 임직원 출퇴근 확인
                  </div>
                </td>
              </tr>
              
              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ marginLeft: '5.0pt', textAlign: 'left' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      2. 수집하려는 개인정보의 항목
                    </span>
                  </div>

                  <div className='mb-3' style={{ position: 'relative', marginLeft: '10.0pt', textAlign: 'left', fontSize: '13.0pt' }}>
                     출퇴근 기록을 위해 필요한 성명, 사원번호, 휴대전화번호, 출퇴근 시간, 
                     <span style={{textDecoration: 'underline'}}><OverlayTrigger placement="top-start" delay={{ show: 50, hide: 500 }} overlay={근태정보Tooltip}><sup><Badge pill bg="primary" style={{ cursor: "pointer" }}>tip</Badge></sup></OverlayTrigger>바이오정보(지문)</span>
                  </div>                
                </td>
              </tr>

              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ marginLeft: '5.0pt', textAlign: 'left' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      3. 개인정보의 보유 및 이용기간
                    </span>
                  </div>

                  <div className='mb-3' style={{ position: 'relative', marginLeft: '10.0pt', textAlign: 'left', fontSize: '13.0pt' }}>
                     근로기준법 제39조(사용증명서) 및 동법 시행령 제19조(사용증명서의 청구), 근로기준법 제42조(계약서류의 보존) 및 동법 시행령 제22조(보존 대상 서류 등)에 따라 3년간 보관 후 파기
                  </div>
                </td>
              </tr>

              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ marginLeft: '5.0pt', textAlign: 'left' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      4. 동의를 거부할 권리가 있다는 사실 및 동의 거부에 따른 불이익이 있는 경우에는 그 불이익의 내용
                    </span>
                  </div>

                  <div className='mb-3' style={{ position: 'relative', marginLeft: '10.0pt', textAlign: 'left', fontSize: '13.0pt' }}>
                     위 개인정보 수집이용 동의를 거부할 권리가 있습니다. 개인정보 수집이용 동의를 거부하는 경우 임금계산, 임금명세서 교부, 사용증명서 발급에 불이익이 발생할 수 있습니다.
                  </div>
                </td>
              </tr>

              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ textAlign: 'right' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      2023년&nbsp;&nbsp; 7월&nbsp;&nbsp; 31일
                    </span>
                  </div>
                </td>
              </tr>

              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ textAlign: 'right' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      작성자 성명: &nbsp;&nbsp; 홍길동&nbsp;&nbsp; (서명)
                    </span>
                  </div>
                </td>
              </tr>


              <tr>
                <td colSpan="2" valign="middle" className="TDStyle1">
                  <div className='mb-3' style={{ marginLeft: '5.0pt', textAlign: 'left' }}>
                    <span style={{ position: 'relative', fontSize: '13.0pt', fontWeight: 'bold' }}>
                      주식회사 ㅇㅇㅇㅇ 대표이사 귀하
                    </span>
                  </div>
                </td>
              </tr>
              

              <tr>
                <td colSpan="2" valign="middle" style={{
                  overflow: 'hidden',
                  width: '700px',
                  height: '17px',
                  borderLeft: '#000000 0.425250pt solid',
                  borderRight: '#000000 0.425250pt solid',
                  borderTop: '#000000 0.425250pt none',
                  borderBottom: '#000000 0.425250pt solid',
                  padding: '1.4pt 5.1pt 1.4pt 5.1pt'
                }}>
                  <div className='mb-3'/>
                </td>
              </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>
    </>
  );
}

export { PersonalInfoAgreement };