import { useState } from 'react';
import { Tab, Tabs, Stack } from 'react-bootstrap';
import { useLocation } from 'react-router-dom';
import { useMediaQuery } from 'react-responsive';
import { contentsDetail } from '../contents';

export const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({
    query: "(max-width:768px)"
  });

  return <>{isMobile && children}</>
}

export const PC = ({ children }) => {
  const isPc = useMediaQuery({
    query: "(min-width:769px)"
  });

  return <>{isPc && children}</>
}


function Detail() {
  const [key, setKey] = useState('상품설명');

  const { state } = useLocation();

  return (
    <div>
      <PC>
      <Stack gap={0} className="main-bg">
        <div style={{fontWeight: '700'}}>{state.명칭}</div>
        <div style={{fontSize: '1.1rem'}}>{state.디테일지원액1}</div>
        <div style={{fontSize: '1.1rem'}}>{state.디테일지원액2}</div>
      </Stack>
      </PC>
      <Mobile>
      <Stack gap={0} className="main-bg">
        <div style={{fontWeight: '700'}}>{state.명칭}</div>
        <div style={{fontSize: '1rem'}}>{state.디테일지원액1}</div>
        <div style={{fontSize: '1rem'}}>{state.디테일지원액2}</div>
      </Stack>
      </Mobile>
      <Tabs
        id="controlled-tab-example"
        activeKey={key}
        onSelect={(k) => setKey(k)}
      >      
        <Tab eventKey="상품설명" title="&nbsp;상품설명&nbsp;">
          <div>
          {contentsDetail[state.DetailId].상품설명}
          </div>
        </Tab>
        {
          contentsDetail[state.DetailId].단계.map((item, idx) => {
            return (
              <Tab eventKey={idx+1} title={idx+1+'단계'} key={idx}>
                {item}
              </Tab>
            )
          })
        }                  
      </Tabs>
    </div>
  );
}

export default Detail;