import { Accordion, Table, ButtonGroup, ToggleButton, Badge } from 'react-bootstrap';
import ApplicationFormTen from '../applicationform/ApplicationFormTen';
import { PersonalInfoAgreement } from '../applicationform/PersonalInfoAgreement';

function ContentId10StageFirst() {

  const commuteManage = [
    { id: "0", system: "타임레코더(출퇴근기록기)", operation: "출퇴근카드를 기록기에 투입하여, 출퇴근 시간을 자동으로 카드에 기록하는 방식", cost: "20~30만원" },
    { id: "1", system: "지문인식기", operation: "근로자의 지문을 사전등록 후 출퇴근 시 지문을 인식시키는 방식(출입통제 등 보안시스템과 연계 가능", cost: "20~50만원" },
    { id: "2", system: "RF카드 인식기", operation: "근로자별 고유ID가 부여된 전자카드를 출퇴근 시 인식시키는 방식(지문 인식기에 RF카드 인식기능이 포함되는 제품도 있음", cost: "20~50만원" },
    { id: "3", system: "모바일(비콘단말기)", operation: "블루투스통신이 되는 비콘단말기의 신호범위 내에서 스마트폰 App등으로 출퇴근 기록", cost: "500~2000원/1인당(월 사용료)" },
    { id: "4", system: "모바일(WIFI인증)", operation: "근무장소의 와이파이 연결 시에만 스마트폰 App 등으로 출퇴근 기록", cost: "500~2000원/1인당(월 사용료)" },
    { id: "5", system: "모바일(GPS인증)", operation: "승인된 위치나 반경 범위 내에서 스마트폰 App 등으로 출퇴근 기록", cost: "500~2000원/1인당(월 사용료)" }
  ];

  return (
    <>
      <div className="detail-header">
        【1단계 따라하기】 워크넷에서 고용노동부 취업희망풀 대상자를 검색하고 고용센터에 채용지원서비스 신청
      </div>
      <Accordion defaultActiveKey={["0"]}>
        <Accordion.Item eventKey="0">
          <Accordion.Header>
            <div>1. 전자적기계적방식의 출퇴근 기록관리가 필요합니다.</div>
          </Accordion.Header>
          <Accordion.Body>
            <Table striped bordered hover>
              <thead>
                <tr>
                  <th>연번</th>
                  <th>시스템명</th>
                  <th>작동방식</th>
                  <th>비용</th>
                </tr>
              </thead>
              <tbody>
                {
                  commuteManage.map((item, idx) => {
                    return (
                      <tr key={item.id}>
                        <td>{item.id}</td>
                        <td>{item.system}</td>
                        <td>{item.operation}</td>
                        <td>{item.cost}</td>
                      </tr>
                    )
                  })
                }
              </tbody>
            </Table>

          </Accordion.Body>
        </Accordion.Item>


        <Accordion.Item eventKey="1">
          <Accordion.Header>
            <div>2. 출퇴근기록관리를 위해 근로자로부터 동의서를 받으세요.</div>
          </Accordion.Header>
          <Accordion.Body>
            <PersonalInfoAgreement />
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </>
  )
}

function ContentId10StageSecond() {
  return (
    <>
      <div>기존 근로계약서를 어떻게 변경해야 하는 지에 대한 비교표</div>
      <div>기존 취업규칙을 어떻게 변경해야 하는 지에 대한 비교표</div>
    </>
  )
}


function ContentId10StageThird() {
  return (
    <>
      <Accordion defaultActiveKey="0">
        <Accordion.Item eventKey="0">
          <Accordion.Header><div>1. 신청서 작성 tip을 참고해서 신청서식을 작성하세요.</div></Accordion.Header>
          <Accordion.Body>
            <ApplicationFormTen />
          </Accordion.Body>
        </Accordion.Item>
      </Accordion>
    </>
  )
}

export { ContentId10StageFirst, ContentId10StageSecond, ContentId10StageThird }