import { Nav, Navbar, Container } from 'react-bootstrap';
import ApplicationFormThreeMobile from './applicationform/ApplicationFormThreeMobile';

function MainNav() {

  const handleScrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };
    
  return (
    <>
    {/* <div style={{marginTop: "30px", marginBottom: "30px"}}>
    <ApplicationFormThreeMobile />
    </div> */}
        <Navbar bg="dark" data-bs-theme="dark" sticky="top">
          <Container>
          <Navbar.Brand href="/">캐쉬토스</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link onClick={handleScrollToTop}>맨위로</Nav.Link>            
            </Nav>
          </Container>
        </Navbar>
    </>
  );
}

export default MainNav;