import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Container, Col, Row, Form, Button, Accordion, Card, Stack } from 'react-bootstrap';
import { contentsList, contents } from './contents';
import axios from 'axios';
import { changeBrno, changeOwnerName, changestartBrno, addSaeopJang } from './store';

function MainAccordion() {

  let user = useSelector((state) => state.user)
  let kcomwel = useSelector((state) => state.kcomwel)
  let dispatch = useDispatch()

  // API Key와 url을 저장 --> 나중에 암호화필요
  const apiKey = process.env.REACT_APP_API_KEY;
  const 국세청url = `https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=${apiKey}`;
  const 근복단url = `http://apis.data.go.kr/B490001/gySjbPstateInfoService/getGySjBoheomBsshItem?serviceKey=${apiKey}&opaBoheomFg=2&v_saeopjaDrno=${user.brno}`

  const navigate = useNavigate();

  const [activeKey, setActiveKey] = useState('1'); //열린상태로 보여줘야하는 아코디온의 eventKey값을 저장
  const [isSecondVisible, setIsSecondVisible] = useState(false); //두번째 아코디온을 보여줄 것인지를 결정하는 bool 변수
  const [isThirdVisible, setIsThirdVisible] = useState(false); //세번째 아코디온을 보여줄 것인지를 결정하는 bool 변수
  const [isFourthVisible, setIsFourthVisible] = useState(false); //네번째 아코디온을 보여줄 것인지를 결정하는 bool 변수
  const [selectedStatus, setSelectedStatus] = useState('');
  const [selectedInterestId, setSelectedInterestId] = useState('');
  const [selectedInterestValue, setSelectedInterestValue] = useState('');

  const [국세청인증, 변경국세청인증] = useState(true);
  const [tmpValue, setTmpValue] = useState(''); //라디오버튼으로 선택된 Value를 임시로 저장하는 변수

  //선택된 아코디언의 eventKey값을 변경해주는 함수
  const handleAccordionChange = (eventKey) => {
    setActiveKey(eventKey);
  };

  //선택된 현재상황을 value값을 변경해주는 함수
  const handleStatusRadioChange = (event) => {
    setTmpValue(event.target.value);
  };

  //선택된 관심분야의 Id값과 Value값을 임시변수에 저장해주는 함수
  const handleInterestRadioChange = (event) => {
    setTmpValue(event.target.value); //선택된 Value를 임시 변수에 저장
  };

  //첫번째 아코디온에 있는 "다음"버튼을 클릭했을때 호출되는 함수
  const handleFirstAccordionClick = () => {
    setIsSecondVisible(true); //두번째 아코디온의 bool을 true로 변경하여 화면에 보여지도록 변경
    setSelectedStatus(tmpValue); //임시변수에 저장된 Value를 최종 변수에 저장
    setActiveKey('2'); //열린상태로 보여줘야하는 아코디온의 eventKey값을 "2번"으로 변경
    setTmpValue('') //tmpValue값을 초기화
  };

  //두번째 아코디온에 있는 "다음"버튼을 클릭했을때 호출되는 함수
  const [isLoading, setIsLoading] = useState(false);
  const handleSecondAccordionClick = () => {
    setIsLoading(true); 
    axios.post(국세청url, { b_no: [user.brno] })
      .then((결과) => (
        결과.data.match_cnt === 1
          ? (
            // 근복단 API 호출 하는 영역
            axios.get(근복단url).then(
              (결과) => {
                변경국세청인증(true);
                setIsThirdVisible(true); //세번째 아코디온의 bool을 true로 변경하여 화면에 보여지도록 변경
                setActiveKey('3'); //열린상태로 보여줘야하는 아코디온의 eventKey값을 "3번"으로 변경
                setTmpValue(''); //tmpValue값을 초기화
                dispatch(addSaeopJang(결과.data.response.body.items.item));
                setIsLoading(false);
              }
            )
          )
          : (alert("사장님이 아니시군요!"), setIsLoading(false))
      )
      )
      .catch(() => { alert("앗! 수리중입니다."); setIsLoading(false); });
  };

  //세번째 아코디온에 있는 "다음"버튼을 클릭했을때 호출되는 함수
  const handleThirdAccordionClick = () => {
    setIsFourthVisible(true); //네번째 아코디온의 bool을 true로 변경하여 화면에 보여지도록 변경
    setSelectedInterestValue(tmpValue); //임시변수에 저장된 Value를 최종 변수에 저장
    setSelectedInterestId(tmpValue); //임시변수에 저장된 Id를 최종 변수에 저장
    setActiveKey('4'); //열린상태로 보여줘야하는 아코디온의 eventKey값을 "4번"으로 변경
    setTmpValue('') //tmpValue값을 초기화
  };

  //contents의 value를 찾는 함수
  const findInterestValue = contentsList.find((data) => (data.id === selectedInterestValue));

  // console.log(user)
  // console.log(kcomwel)

  return (
    <>
      <Stack gap={0.5} className="main-bg">
        <div>사장님이 받을 수 있는 <span style={{ fontWeight: "700" }}>정부지원금</span>을</div>
        <div><span style={{ fontWeight: "700" }}>신청하는 방법</span>을 알려드려요.</div>
      </Stack>
      <Accordion activeKey={activeKey} onSelect={handleAccordionChange}>
        {/* 첫번째 아코디온 시작 */}
        <Accordion.Item eventKey="1">
          <Accordion.Header>
            <div className='accordion-header-font'>
              {isSecondVisible === false
                ? <div style={{ fontWeight: "700" }}>현재 상황을 선택하세요.</div>
                : <div>
                  <div>현재 상황</div>
                  <span style={{ fontWeight: "700" }}>{selectedStatus}</span>
                </div>
              }
            </div>
          </Accordion.Header>
          <Accordion.Body>
            <Form className='accordion-body-font'>
              <Form.Check
                inline
                type="radio" name="statusRadioGroup" label="창업준비" value="창업준비" onChange={handleStatusRadioChange} />
              <Form.Check
                inline
                type="radio" name="statusRadioGroup" label="영업중" value="영업중" onChange={handleStatusRadioChange} />
            </Form>
            <Button className='button-body-font' onClick={handleFirstAccordionClick}>다음</Button>
          </Accordion.Body>
        </Accordion.Item>

        {/* 첫번째 라디오가 "창업준비"일때 두번째 아코디온 시작 */}
        {isSecondVisible && selectedStatus === "창업준비" && (
          <Accordion.Item eventKey="2">
            <Accordion.Header>
              <div className='accordion-header-font'>공사중입니다.</div>
            </Accordion.Header>
            <Accordion.Body>
            </Accordion.Body>
          </Accordion.Item>
        )}

        {/* 첫번째 라디오가 "영업중"일때 두번째 아코디온 시작 */}
        {isSecondVisible && selectedStatus === "영업중" && (
          <Accordion.Item eventKey="2">
            <Accordion.Header>
              <div className='accordion-header-font'>
                {isThirdVisible === false
                  ? <div style={{ fontWeight: "700" }}>사업자등록증 정보를 입력하세요.</div>
                  : 국세청인증 === true ? <div>
                    <div>사업자등록번호</div>
                    <span style={{ fontWeight: "700" }}>{user.brno}</span>
                  </div>
                    : null
                }
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <Form className='accordion-body-font'>
                <Row className='mb-3'>
                  <Form.Group as={Col} md="4" controlId="inputBrno" className='mb-2'>
                    <Form.Label>사업자등록번호</Form.Label>
                    <Form.Control
                      className='accordion-body-font'
                      required
                      type="text" placeholder="사업자등록번호입력(예:1111111111)"
                      onChange={(e) => { dispatch(changeBrno(e.target.value)) }} />
                  </Form.Group>
                  <Form.Group as={Col} md="4" controlId="ownerName" className='mb-2'>
                    <Form.Label>대표자명</Form.Label>
                    <Form.Control
                      className='accordion-body-font'
                      required
                      type="text" placeholder="대표자명입력(예:홍길동)"
                      onChange={(e) => { dispatch(changeOwnerName(e.target.value)) }} />
                  </Form.Group>
                  <Form.Group as={Col} md="4" controlId="startDate">
                    <Form.Label>개업일자</Form.Label>
                    <Form.Control
                      className='accordion-body-font'
                      required
                      type="text" placeholder="개업일자입력(예:20220102)"
                      onChange={(e) => { dispatch(changestartBrno(e.target.value)) }} />
                  </Form.Group>
                </Row>
              </Form>
              <Button className='button-body-font' disabled={isLoading} onClick={!isLoading ? handleSecondAccordionClick : null}>
                {isLoading ? 'Loading…' : '사장님인증'}
              </Button>


            </Accordion.Body>
          </Accordion.Item>
        )}

        {/* 첫번째 라디오가 "영업중"일때 세번째 아코디온 시작 */}
        {isThirdVisible && 국세청인증 && selectedStatus === "영업중" && (
          <Accordion.Item eventKey="3">

            {/* 컨텐츠 리스트를 라디오버튼으로 보여주고 선택하게 만드는 부분 */}
            <Accordion.Header>
              <div className='accordion-header-font'>
                {isFourthVisible === false
                  ? <div style={{ fontWeight: "700" }}>관심있는 분야를 선택하세요.</div>
                  : <div>
                    <div>관심분야</div>
                    <span style={{ fontWeight: "700" }}>{findInterestValue && findInterestValue.value}</span>
                  </div>
                }
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <Form className='accordion-body-font'>
                {
                  contentsList.map((item, idx) => {
                    return (
                      <span key={item.id}>
                        <Form.Check
                          inline
                          type="radio"
                          name="interestRadioGroup"
                          label={item.value}
                          value={item.id}
                          onChange={handleInterestRadioChange}
                        />
                      </span>
                    )
                  })
                }
              </Form>
              <Button className='button-body-font' onClick={handleThirdAccordionClick}>다음</Button>
            </Accordion.Body>
          </Accordion.Item>
        )}

        {isFourthVisible && selectedStatus === "영업중" && (
          <Accordion.Item eventKey="4">
            <Accordion.Header>
              <div className='accordion-header-font'>
                <div style={{ fontWeight: "700" }}>캐쉬토스가 추천하는 지원금</div>
                <span style={{ fontWeight: "700" }}>{contents[selectedInterestValue].length} 건</span>
              </div>
            </Accordion.Header>
            <Accordion.Body>
              <Container>
                <Row md='auto'>
                  {
                    contents[selectedInterestId].map((item, idx) => {
                      return (
                        <span key={item.id}>
                          <Col md='auto' onClick={() => { navigate('/detail', { state: item }) }}>
                            <Card className='card-size' border="primary">
                              <Card.Header style={{fontSize: '1rem', fontWeight: '700'}}>{item.명칭}</Card.Header>
                              <Card.Body>
                                <Card.Title style={{fontSize: '1rem', fontWeight: '700'}}>{item.지원대상}에게</Card.Title>
                                <Card.Text style={{fontSize: '1rem'}}>
                                  <div>{item.카드설명}</div> 
                                  <div>➜ {item.지원한도}</div>
                                </Card.Text>
                              </Card.Body>
                            </Card>
                          </Col>
                        </span>
                      )
                    })
                  }
                </Row>
              </Container>
            </Accordion.Body>
          </Accordion.Item>
        )}
      </Accordion>
    </>
  );
}

export default MainAccordion;

