import { configureStore, createSlice } from '@reduxjs/toolkit'

let user = createSlice({
  name : 'user',
  initialState : {
    brno: '1111111111',
    ownerName: '대표자이름',
    startBrno: '20200208',
    brnoType: '4444',
    brnoIndustry: '5555'},
  reducers : {
    changeBrno(state, action){
      state.brno = action.payload
    },
    changeOwnerName(state, action){
      state.ownerName = action.payload
    },
    changestartBrno(state, action){
      state.startBrno = action.payload
    },
    changebrnoType(state, action){
      state.brnoType = action.payload
    },
    changebrnoIndustry(state, action){
      state.brnoIndustry = action.payload
    }
  }
})

let kcomwel = createSlice({
  name : 'kcomwel',
  initialState : [
    {
      addr: '',
      opaBoheomFg: '',
      post: '',
      saeopFg: '',
      saeopjaDrno: '',
      saeopjangNm: '',
      sangsiInwonCnt: '',
      seongripDt: '',
      sjEopjongCd: '',
      sjEopjongNm: ''
    }
  ],
  reducers : {
    addSaeopJang(state, action) {
      state.push(action.payload)
    }
  }
})

let workersCount = createSlice({
  name : 'workersCount',
  initialState : {
      thisMonthWorkers: kcomwel.sangsiInwonCnt,
      lastMonthWorkers: kcomwel.sangsiInwonCnt,
      lastLastMonthWorkers: kcomwel.sangsiInwonCnt
    },
  reducers : {
    changeThisMonthWorkers(state, action){
      state.thisMonthWorkers = action.payload
    },
    changeLastMonthWorkers(state, action){
      state.lastMonthWorkers = action.payload
    },
    changeLastLastMonthWorkers(state, action){
      state.lastLastMonthWorkers = action.payload
    },
  }
})

export let { changeBrno, changeOwnerName, changestartBrno, changebrnoType, changebrnoIndustry } = user.actions
export let { addSaeopJang } = kcomwel.actions
export let { changeThisMonthWorkers, changeLastMonthWorkers, changeLastLastMonthWorkers } = workersCount.actions

export default configureStore({
  reducer: {
    user : user.reducer,
    kcomwel : kcomwel.reducer,
    workersCount : workersCount.reducer
  }
}) 