function ContentId3StageSecond() {

  const [showModal, setShowModal] = useState(false);

  const handleImageClick = () => {
    setShowModal(true);
  };

  const handleCloseModal = () => {
    setShowModal(false);
  };


  const checkContents = [
    '신규 채용 3개월 이전부터 신규 채용 후 6개월 이후까지 어떤 근로자도 해고하면 안됩니다.(단, 자진퇴사는 상관없습니다.)',
    '신규 채용 직원이 계약직이면 지원금을 받을 수 없습니다.',
    '신규 채용 직원의 일주일 소정근로시간이 15시간 보다 크거나 같아야 지원금을 받을 수 있습니다.(예를 들어, 하루 3시간씩 월~금까지 일하면 일주일 소정근로시간이 15시간이므로 지원금을 받을 수 있습니다.)',
    '신규 채용 직원이 1년 이내에 근무한 적이 있는 사람이라면 지원금을 받을 수 없습니다.'
  ];
  const [checkboxes, setCheckboxes] = useState([false, false, false, false]);
  const [canProceed, setCanProceed] = useState(false);

  const [isSecondVisible, setIsSecondVisible] = useState(false);
  const [activeKey, setActiveKey] = useState('0')

  const handleAccordionChange = (eventKey) => {
    setActiveKey(eventKey);
  };

  const handleCheckboxChange = (index) => {
    const updatedCheckboxes = [...checkboxes];
    updatedCheckboxes[index] = !updatedCheckboxes[index];
    setCheckboxes(updatedCheckboxes);

    const allChecked = updatedCheckboxes.every((checkbox) => checkbox);
    setCanProceed(allChecked);
  };

  const handleProceed = () => {
    if (canProceed) {
      // Proceed to the next step
      setIsSecondVisible(true);
      setActiveKey('1')
    } else {
      alert('모든 체크박스를 체크해주세요.');
    }
  };

  return (
    <>
      <div className="detail-header">
        【2단계 따라하기】 취업희망풀 대상자를 신규 직원을 채용
      </div>
      <Accordion  activeKey={activeKey} onSelect={handleAccordionChange}>
        <Accordion.Item eventKey="0">
          <Accordion.Header><div className='accordion-header-font'>1. 지원금을 받기 위한 신규 직원 채용 유의사항입니다. 체크하면서 모두 읽어보세요. </div></Accordion.Header>
          <Accordion.Body>
            <Form>
              {
                isSecondVisible ?
                  checkboxes.map((seq, index) => (
                    <div key={index} className='accordion-body-font'>
                      <Form.Check
                        disabled
                        type="checkbox"
                        checked={seq}
                        label={checkContents[index]}
                        onChange={() => handleCheckboxChange(index)}
                      />
                    </div>
                  ))
                  :
                  checkboxes.map((seq, index) => (
                    <div key={index} className='accordion-body-font'>
                      <Form.Check
                        type="checkbox"
                        checked={seq}
                        label={checkContents[index]}
                        onChange={() => handleCheckboxChange(index)}
                      />
                    </div>
                  ))
              }
            </Form>
            {
              isSecondVisible ? null : <Button onClick={handleProceed}>체크완료</Button>
            }
          </Accordion.Body>
        </Accordion.Item>
        {
          isSecondVisible ?
            <Accordion.Item eventKey="1">
              <Accordion.Header><div className='accordion-header-font'>2. 아래 근로계약서 작성 tip을 참고해서 작성하세요.<br />(계약직 근로자는 지원금을 못 받습니다.)</div></Accordion.Header>
              <Accordion.Body>
                <PC><LaborContractStage /></PC>
                {/* <PC><LaborContractStageMobile /></PC> */}
                <Mobile>
                  <div><img src="/L-001.png" alt="사진" className='image-body' onClick={handleImageClick} /></div>
                  {showModal && (
                    <ImageModal imageUrl="/L-001.png" onClose={handleCloseModal} />
                  )}
                </Mobile>
              </Accordion.Body>
            </Accordion.Item>
            : null
        }
      </Accordion>
    </>
  )
};

